@tailwind base;
@tailwind components;
@tailwind utilities;

/* Flagyard-inspired Dark Theme */
:root {
  --bg-primary: #0a0a0f;
  --bg-secondary: #12121a;
  --bg-tertiary: #1a1a24;
  --text-primary: #ffffff;
  --text-secondary: #9ca3af;
  --accent-pink: #ff0066;
  --accent-purple: #8b5cf6;
  --accent-blue: #3b82f6;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg-primary);
  color: var(--text-primary);
  overflow-x: hidden;
}

/* Neon glow effects */
.neon-text {
  text-shadow: 0 0 10px currentColor, 0 0 20px currentColor, 0 0 40px currentColor;
}

.neon-border {
  box-shadow: 0 0 5px currentColor, inset 0 0 5px currentColor;
}

/* Glass morphism effect */
.glass {
  background: rgba(18, 18, 26, 0.7);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Animated gradient background */
.animated-gradient {
  background: linear-gradient(-45deg, #ff0066, #8b5cf6, #3b82f6, #10b981);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}

@keyframes gradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Cyber grid background */
.cyber-grid {
  background-image: 
    linear-gradient(rgba(255, 0, 102, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 0, 102, 0.1) 1px, transparent 1px);
  background-size: 50px 50px;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, var(--accent-pink), var(--accent-purple));
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-pink);
}

/* Card hover effects */
.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(255, 0, 102, 0.3);
}

/* Badge styles */
.difficulty-badge {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.badge-easy {
  background: rgba(34, 197, 94, 0.2);
  color: #22c55e;
  border: 1px solid rgba(34, 197, 94, 0.3);
}

.badge-medium {
  background: rgba(251, 191, 36, 0.2);
  color: #fbbf24;
  border: 1px solid rgba(251, 191, 36, 0.3);
}

.badge-hard {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.3);
}

/* Category icons background */
.category-gradient-web {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.category-gradient-pwn {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
}

.category-gradient-crypto {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
}

.category-gradient-reverse {
  background: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%);
}

.category-gradient-forensics {
  background: linear-gradient(135deg, #fa709a 0%, #fee140 100%);
}

.category-gradient-misc {
  background: linear-gradient(135deg, #30cfd0 0%, #330867 100%);
}
